import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { from, observable } from 'rxjs';
import { Register2 } from './register/Register2';

// import { Register2 } from "../app/register";
@Injectable({
  providedIn: 'root',
})
export class LoginService {
  Url: string;
  token: string;
  header: any;
  constructor(private http: HttpClient) {
    this.Url = 'http://localhost:1834/api/login';
    const headerSettings: { [name: string]: string | string[] } = {};
    this.header = new HttpHeaders(headerSettings);
  }
  Login(model: any) {
    const a = this.Url + 'UserLogin';
    return this.http.post<any>(this.Url + 'UserLogin', model, {
      headers: this.header,
    });
  }
  Createuser(register: Register2) {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' }),
    };
    return this.http.post<any>(
      this.Url + 'createcontact/',
      register,
      httpOptions
    );
  }
}
