import { Injectable } from '@angular/core';

@Injectable()
export class Register2 {
  constructor(public UserName: string, public LoginName: string, public Password: string, public Email: string, public ContactNo: string, public Address: string) {
    this.UserName = UserName;
    this.LoginName = UserName;
    this.Password = UserName;
    this.Email = UserName;
    this.ContactNo = UserName;
    this.Address = UserName;

  }
}
